body{
  background-color: aqua;
  background-image: url('https://img.freepik.com/free-vector/futuristic-background-design_23-2148503793.jpg?size=626&ext=jpg&ga=GA1.1.2008272138.1721606400&semt=ais_user');
  background-repeat: no-repeat;
  background-size: cover;
 
  font-family: Arial, Helvetica, sans-serif;
}

.flex{
  max-width: 450px;
  padding: 20px;
  border-radius: 5px;
}
.register{
  margin: 50px auto;
  width: 25%;
  padding: 40px;
  border-radius: 10px;
  box-shadow: 1px 1px 8px rgb(247, 4, 210);
  background-color:lightcyan;


}
.col-1 .flex{
  width: 100%;
  display: flex;
  flex-direction: column;
}
.col-1 .flex label{
  margin-top: 10px;
  text-transform: capitalize;
  font-weight: bold;
}
.col-1 .flex input{
  position: relative;
  margin-top: 5px;
  padding: 10px;
  width: 95%;
  height: 30px;
  background: transparent;
  font-size: 16px;
  display: flex;
  border: none;
  border-radius: 40px;
  
}

.flex button{
  margin-top: 10px;
  height: 40px;
  background-color: green;
  border: 1px solid blueviolet;
  border-radius: 10px;
  width: 250px;
  font-weight: bolder;
  text-transform: uppercase;
}
.register .col-1 h2{
  text-align: center;
  color: rebeccapurple;
  text-transform: uppercase;
  font-weight: bold;
  text-shadow: 1px 1px 2px rgb(241, 13, 13);

}
.btn{
  border-radius: 10px;

}
.btn:hover{
  color: red;
  background-color: greenyellow;
}
.col-1 .flex .icon{
  position: fixed;
  top:26%;
  left:500px;
  translate: 0 -50%;
  font-size: 16px;
  }
  .col-1 .flex .icon1{
    position: fixed;
    top:36%;
    left:500px;
    translate: 0 -50%;
  font-size: 16px;
    }
    .col-1 .flex .icon2{
      position: fixed;
      top:45%;
      left:500px;
      translate: 0 -50%;
  font-size: 16px;
      }

